<template>
  <div class="page-content config-page">
    <el-row>
      <el-col>
        <el-form :inline="true">
          <el-form-item>
            <el-date-picker placeholder="" v-model="formdata.date" type="daterange" align="left" unlink-panels range-separator="" start-placeholder="" end-placeholder="" :picker-options="pickerOptions">
            </el-date-picker>
          </el-form-item>

          <el-form-item>
            <el-select v-model="formdata.platform" placeholder="">
              <el-option label="Pad" value="pad"></el-option>
              <el-option label="iOS" value="ios"></el-option>
              <el-option label="android" value="android"></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>

          <el-form-item>
            <el-cascader :options="cityData" :props="cityForProps" placeholder="">
            </el-cascader>
          </el-form-item>

          <el-form-item style="margin-bottom:0">
            <el-button type="primary"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>


    <div style="margin-top: 20px">

      <ve-line :data="chartData" :settings="chartSettings"></ve-line>
    </div>

  </div>
</template>

<script>
import CityData from "../../json/city.json";

export default {
  data() {
    this.chartSettings = {
        stack: { '': ['', ''] },
        area: true
      }
    return {
      cityData: CityData,
      cityForProps : { 
        value: 'name',
        label: 'name',
        children: 'list'
      },
      formdata: {
        date: "",
        platform: "",
        city: ""
      },
      pickerOptions: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      chartData: {
          columns: ['', '', '', ''],
          rows: [
            { '': '1/1', '': 1393, '': 1093, '': 0.32 },
            { '': '1/2', '': 3530, '': 3230, '': 0.26 },
            { '': '1/3', '': 2923, '': 2623, '': 0.76 },
            { '': '1/4', '': 1723, '': 1423, '': 0.49 },
            { '': '1/5', '': 3792, '': 3492, '': 0.323 },
            { '': '1/6', '': 4593, '': 4293, '': 0.78 }
          ]
        }
    };
  }
};
</script>



